import Title from './Title'
import AboutMeButton from './AboutMeButton'
import PortfolioButton from './PortfolioButton'
import ArtistsButton from './ArtistsButton'
import NowPlaying from './NowPlaying'
import Bitcoin from './Bitcoin'
import Artist from './Artist'
// import Particles from 'react-particles-js'
import './styles/Home.css'

const Container = ({ showAboutMe, setShowAboutMe, showArtists, setShowArtists, ourTrack, isPlaying, bitcoinPrice, topArtist }) => {
    return (
        <div className={`container `} onClick={() => {showAboutMe && setShowAboutMe(false); showArtists && setShowArtists(false);}} >
            {/* ${showAboutMe || isPlaying ? 'hide' : ''} */}
            <Title />
            <AboutMeButton onPress={() => setShowAboutMe(!showAboutMe)} />
            {/* <PortfolioButton /> */}
            {/* <ArtistsButton onPress={() => setShowArtists(!showArtists)} /> */}
            {isPlaying && <NowPlaying track={ourTrack} />}
            {/* <Particles id='particles-js' /> */}
            <Artist topArtist={topArtist} />
            <Bitcoin price={bitcoinPrice} />
        </div>
    )
}

export default Container
